.tlbx-table th,
.tlbx-table td:not([scope]) {
  text-align: center;
  vertical-align:middle;
}

.tlbx-contrast-color {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.tlbx-contrast-color .tlbx-contrast-color-thumb {
  margin-right: 10px;
}

.tlbx-contrast-color-thumb,
.tlbx-contrast-none {
  display: -ms-inline-flexbox;
  display: inline-flex;
  width: 40px;
  height: 40px;
}

.tlbx-contrast-color-thumb {
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  font-weight: bold;
  font-size: 1.2rem;
}

.tlbx-contrast-none {
  position: relative;
  background-color: #eee;
  overflow: hidden;
}

.tlbx-contrast-none::after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  top: 1px;
  bottom: 1px;
  border-width: 40px;
  border-style: solid;
  border-color: transparent white;
}

.tlbx-contrast-text {
  display: inline-block;
  width: 100%;
  font-weight: bold;
  font-size: 2rem;
  text-align: center;
}

.tlbx-color-swatch-color .btn {
  opacity: 0;
  -webkit-transition: opacity 200ms;
  -o-transition: opacity 200ms;
  transition: opacity 200ms;
}

.tlbx-color-swatch-color:hover .btn {
  opacity: 1;
}



